body {
  margin: 0;
  padding: 0;
  background-color: #333333;
}

.container,
.container2,
.container3 {
  position: relative;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}

.container {
  margin-top: -8rem;
  background-image: url("bg/bg4.jpg");
  height: 50rem;
}

.container::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.39);
}

.container h1{
  color: #816797;
  position: absolute;
  top: 13rem;
  left: 13.2rem;
  font-size: 3.2rem;
  /* color: white; */
  font-family: sans-serif;
}

.container p{
