  font-weight: lighter;
  font-family: sans-serif;
}

.button{
  position: absolute;
  top: 32rem;
  left: 13.5rem;
  font-size: 1.2rem;
  font-weight: lighter;
  font-family: sans-serif;
  border: 3px solid #202b3b;
  border-radius: 1rem;
  padding: 0.9rem ;
  transition: 0.5s;
}

.button a{
  font-family: sans-serif;
